.adminRegister {
  justify-content: center;
  margin-top: 10% !important;
  width: 35%;
  margin: 15px auto;
  background-color: #fff;
  border-radius: 7px;
  box-shadow: 0 1px 2px rgb(0, 0, 0, 0.2);
  padding: 1%;
}

.have-an-account {
  margin-bottom: 5% !important;
}

.dashboard {
  justify-content: center;
  margin-top: 20% !important;
  width: 35%;
  margin: 15px auto;
  background-color: #fff;
  border-radius: 7px;
  box-shadow: 0 1px 2px rgb(0, 0, 0, 0.2);
  padding: 2%;
}
.adminRegister-student {
  justify-content: center;
  margin-top: 5% !important;
  width: 35%;
  margin: 15px auto;
  background-color: #fff;
  border-radius: 7px;
  box-shadow: 0 1px 2px rgb(0, 0, 0, 0.2);
  padding: 1%;
}

@media screen and (max-width: 1200px) {
  .adminRegister {
    border-radius: 5px;
    width: fit-content;
    margin-top: 100px !important;
    margin: 0px auto;
    padding-right: 60px;
  }
}
